import React from 'react';
import Code from './../shared/code';
import Comments from './../shared/comments';
import PageTitle from './../shared/page-title';
import Paragraph from './../shared/paragraph';
import Link from './../shared/link';
import InlineCode from './../shared/inline-code';

export const date = '09 Mar 2013';
export const title = 'Something you probably didn’t know about C/C++';

export default class Page extends React.Component {
    render() {
        return (
            <div>
                <PageTitle text={title} date={date}/>

                <Paragraph>
                    I love to collect mind-blowing and not too widely known C/C++
                    language constructions and facts. This post carries my collection and will
                    be updated when new entries appear.
                </Paragraph>

                <Paragraph>
                    C/C++
                </Paragraph>

                <Code lang="c++">
{`// zero-sized bit fields
struct S
{
  int a;
  int b:2;
  int c:6,
  :0;
  int d;
};`}
                </Code>

                <Paragraph>
                    C-only
                </Paragraph>

                <Code lang="c++">
{`// Exit code when there's no return statement?
#include <stdio.h>
int main()
{
  printf("Test\\n");
}

// The program exit code will be... ? 5. When there is no return statement,
// the program will return the result of the last executed function.
// In this case it is 5, because printf returns the number of symbols actually printed.`}
                </Code>

                <Paragraph>
                    C/C++
                </Paragraph>

                <Code lang="c++">
{`// a[b] is practically the same as *(a + b)
char c = 4["Hello, world!"]; // c == 'o'`}
                </Code>

                <Paragraph>
                    C/C++
                </Paragraph>

                <Code lang="c++">
{`int x = 33;
int f()
{
  int x = 1045;
  {
    return x; // returns 1045
  }
}

int x = 33;
int f()
{
  int x = 1045;
  {
    extern int x;
    return x; // returns 33
  }
}`}
                </Code>

                <Paragraph>
                    C-only
                </Paragraph>

                <Code lang="c++">
{`// Passed array must have at least 10 elements (-Warray-bounds):
void f(int arr[static 10]) { }

// Free compile-time not-NULL check [-Wnonnull]
void f(int arr[static 1]) { }`}
                </Code>

                <Paragraph>
                    C-only
                </Paragraph>

                <Code lang="c++">
{`int x = 'FOO!'; // single quotes! codepad.org compiler says x == 1179602721`}
                </Code>

                <Paragraph>
                    C/C++
                </Paragraph>

                <Code lang="c++">
{`int x = y+++z; // parsed as y++ + z ("greedy lexer rule")
int a = b+++++c; // b++ ++ + c, not b++ + ++c`}
                </Code>

                <Paragraph>
                    C-only
                </Paragraph>

                <Code lang="c++">
{`// The shortest C-program that compiles and links. With a warning. Do not run!
main;
// No warnings. Do not run either!
int main;`}
                </Code>

                <Paragraph>
                    C-only
                </Paragraph>

                <Code lang="c++">
{`// Old-style function definition
int main(argc, argv)
int argc;
char *argv[];
{
  return 0;
}`}
                </Code>

                <Paragraph>
                    C-only
                </Paragraph>

                <Code lang="c++">
{`return ((int []){1,2,3,4})[1];
// returns 2`}
                </Code>

                <Paragraph>
                    C-only
                </Paragraph>

                <Code lang="c++">
{`// Initializer with designators hell
struct {
   int x;
   struct {
       int y, z;
   } nested;
} i = { .nested.y = 5, 6, .x = 1, 2 };
// i.nested.y == 2, i.nested.z == 6`}
                </Code>

                <Paragraph>
                    GCC C-only
                </Paragraph>

                <Code lang="c++">
{`// omit second component of ?: operator
extern int f();
return f() ? : -1; // Returns f() if it's not zero, or -1 otherwise. Reminds me of C# operator ??`}
                </Code>

                <Paragraph>
                    C/C++
                </Paragraph>

                <Code lang="c++">
{`// zero-sized bit fields
struct S
{
  int a;
  int b:2;
  int c:6,
  :0;
  int d;
};`}
                </Code>

                <Paragraph>
                    Most interesting parts
                    of <Link to="http://nickdesaulniers.github.io/blog/2013/07/25/designated-initialization-with-pointers-in-c/">this</Link> article
                    about designated initialization in C:
                </Paragraph>

                <Code lang="c++">
{`struct point
{
  int x, y;
};
// ...
struct point p = { 1, 2 } // ok, p now has coordinates (1, 2)`}
                </Code>

                <Code lang="c++">
{`struct point p = { .y = 1, .x = 2 } // ok, p now has coordinates (2, 1)`}
                </Code>

                <Code lang="c++">
{`// Re-assign? No problems, but tricky
struct point p = { .x = 1, .y = 2 };
// ...
p = { .x = 10, .y = 20 }; // compilation error
p = (struct point){ .x = 10, .y = 20 }; // works
p = (struct point){ 10, 20 }; // this works too`}
                </Code>

                <Code lang="c++">
{`int distance(struct point a, struct point b);
// ...
distance({ 1, 2 }, { 3, 4 }); // no-no, compilation error
distance((struct point){ 1, 2 }, (struct point) { 3, 4 }); // fine, this compiles`}
                </Code>

                <Code lang="c++">
{`// Initializing pointer members
struct node
{
  char *value;
  struct node *next;
};
// ...
struct node n =
{
  .value = "Hello, world!",
  .next = (&(struct node) { .value = "Next node" }) // wow!
}`}
                </Code>

                <Paragraph>
                    Empty declarations in C:
                </Paragraph>

                <Code lang="c++">
{`typedef;
int typedef const;`}
                </Code>

                <Paragraph>
                    Won’t compile with <InlineCode>-Wall</InlineCode> though.
                </Paragraph>

                <Paragraph>
                    Some people write such code probably hoping that C/C++ has native support for tuples. No, it doesn’t.
                </Paragraph>

                <Code lang="c++">
{`int func()
{
    int a = 1;
    int b = 2;
    int c = 3;
    return (a, b, c);
}

// ...

int d = func(); // d == c == 3, if () operator is not overloaded`}
                </Code>

                <Paragraph>
                    This construction is strange in a way that you probably
                    won’t encounter it very often. For some reasons it’s
                    not commonly used. The <Link to="http://en.cppreference.com/w/cpp/language/function-try-block">function-try-block</Link>:
                </Paragraph>

                <Code lang="c++">
{`int f(int n = 2) try {
  ++n; // increments the function parameter
  throw n;
} catch(...) {
  ++n; // n is in scope and still refers to the function parameter
  assert(n == 4);
  return n;
}`}
                </Code>

                <Comments/>
            </div>
        );
    }
}


